import {
  FaArrowDownLong,
  FaArrowUpLong,
  FaEyeSlash,
  FaEye,
} from "react-icons/fa6";
import imsgChart from "../../../asesets/adminImage/chart.png";
import { Chart as ChartJS } from "chart.js/auto";
import { Bar, Doughnut, Line, PolarArea } from "react-chartjs-2";
import DasBoardSlideProduct from "./dasboardSlideProduct/DasBoardSlideProduct";
import { useEffect, useState, useRef } from "react";
import {
  Getprofile,
  dateGet,
  generateOtpForTpin,
  verifyOtpForTpin,
  generatenewTpin,
  resendOtpsCustomer,
  generateOtpForResendTpin,
  demoapiCheck,
} from "../../../api/login/Login";
import { TiTick, TiTickOutline } from "react-icons/ti";
import "bootstrap/dist/css/bootstrap.min.css";
import "bootstrap/dist/js/bootstrap.bundle.min";
import "./Dashboard.css";
import image from "../../../asesets/banner/transparentLogo.png";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { getUserTpinStatus } from "../../../utils/localStorage";
import CustomMarquee from "./CustomMarquee";
import BBpsservicelist from "./dasboardSlideProduct/BBPSServiceList";
import RetailerDashboard from "./dasboardSlideProduct/RetailerDashboard";
import DashboardOtpModal from "./DashboardOtpModal";
function DasBoardCheck({
  walletData,
  services,
  isInstantpaypresnt = { isInstantpaypresnt },
}) {
  const [data, setData] = useState({});
  const [number, setNumber] = useState("");
  const [loader1, setLoader1] = useState(false);
  const [loader, setLoader] = useState(false);
  const [newPin, setNewPin] = useState("");
  const [confirmPin, setConfirmPin] = useState("");
  const [pinError, setPinError] = useState(false);
  const [confirmError, setConfirmError] = useState(false);

  const [userTpinstatus, setUserTpinstatus] = useState(null);
  const [dataprofile, setDataprofile] = useState(null);
  const [message1, setMessage1] = useState("");
  const [error1, setError1] = useState("");
  const [loading, setLoading] = useState(false);
  const [message2, setMessage2] = useState("");
  const [error2, setError2] = useState("");
  const [message3, setMessage3] = useState("");
  const [error3, setError3] = useState("");
  const storedUserid = localStorage.getItem("userIdToken");
  //console.log("storedUserid", storedUserid);
  const [showNewPin, setShowNewPin] = useState(false);
  const [showConfirmPin, setShowConfirmPin] = useState(false);
  const [timeLeft, setTimeLeft] = useState(180);
  const [isTimerEnded, setIsTimerEnded] = useState(false);
  const [resendLoading, setResendLoading] = useState(false);
  const [initializing, setInitializing] = useState(true);
  const [modalVisibility, setModalVisibility] = useState({
    showModal: false,
    showModal2: false,
    showModal3: false,
    showOverlay: false,
  });

  const currentDates = async () => {
    const date = new Date();
    let day = date.getDate();
    let month = date.getMonth() + 1;
    let year = date.getFullYear();
    let currentDate = `${year}-${month}-${day}`;
    // let currentDate = '2024-1-1'

    try {
      const res = await dateGet(currentDate);
      setData(res?.data?.data);
    } catch (error) {
      console.error(error);
    }
  };
  const handleCloseModal1 = () => {
    setModalVisibility((prev) => ({
      ...prev,
      showModal: false,
      showOverlay: false,
    }));
    setMessage1("");
    setError1("");
  };
  const handleCloseModal2 = () => {
    setModalVisibility((prev) => ({
      ...prev,
      showModal2: false,
      showOverlay: false,
    }));
  };

  const handleOpenModal3 = () => {
    setModalVisibility((prev) => ({
      ...prev,
      showModal3: true,
      showOverlay: true,
    }));
  };

  const handleCloseModal3 = () => {
    setModalVisibility((prev) => ({
      ...prev,
      showModal3: false,
      showOverlay: false,
    }));
  };
  const handleSubmitmodal3 = (e) => {
    setModalVisibility((prev) => ({
      ...prev,
      showModal3: false,
      showModal2: true,
    }));
  };

  const handleNewPinChange = (e) => {
    const pin = e.target.value;
    if (/^\d{0,6}$/.test(pin)) {
      setNewPin(pin);
      setPinError(pin.length !== 4);
    } else {
      setPinError(true);
    }
  };

  const handleConfirmPinChange = (e) => {
    const pin = e.target.value;
    if (/^\d{0,6}$/.test(pin)) {
      setConfirmPin(pin);
      setConfirmError(pin.length !== 4 || pin !== newPin);
    } else {
      setConfirmError(true);
    }
  };

  useEffect(() => {
    currentDates();
    const storedTpinStatus = localStorage.getItem("userTpinstatus");
    setUserTpinstatus(storedTpinStatus);
  }, []);

  useEffect(() => {
    const initializeModals = () => {
      if (userTpinstatus === "CV") {
        setModalVisibility({
          showModal: false,
          showModal2: false,
          showModal3: false,
          showOverlay: false,
        });
      } else if (
        userTpinstatus === "NP" ||
        userTpinstatus === null ||
        userTpinstatus === undefined ||
        userTpinstatus === "" ||
        userTpinstatus == "undefined"
      ) {
        setModalVisibility({
          showModal: true,
          showModal2: false,
          showModal3: false,
          showOverlay: true,
        });
      } else if (userTpinstatus === "OV") {
        setModalVisibility({
          showModal: false,
          showModal2: true,
          showModal3: false,
          showOverlay: true,
        });
      } else {
        setModalVisibility({
          showModal: false,
          showModal2: false,
          showModal3: false,
          showOverlay: false,
        });
      }
    };

    if (userTpinstatus !== null) {
      initializeModals();
    }
  }, [userTpinstatus]);

  const handleSubmitmodal1 = async () => {
    setLoader1(true);

    try {
      const response = await generateOtpForTpin({});
      setLoader1(false);

      if (response && response.data && response.data.error === false) {
        toast.success("verified successfully");
        console.log("tpin generated");
        setMessage1(response.data.message);
        handleCloseModal1();
        handleOpenModal3();
      } else {
        setError1("Failed to generate TPIN");
        toast.error("Failed to generate TPIN");
      }
    } catch (error) {
      setLoader1(false);
      setError1("Failed to generate OTP for Tpin");
      toast.error("Failed to generate TPIN");
    }
  };

  function getLocation() {
    return new Promise((resolve, reject) => {
      if (!navigator.geolocation) {
        return reject("Geolocation is not supported by this browser");
      }

      navigator.geolocation.getCurrentPosition(
        ({ coords }) => {
          resolve({
            latitude: coords.latitude,
            longitude: coords.longitude,
          });
        },
        (error) => reject(error)
      );
    });
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (
      newPin.length !== 4 ||
      confirmPin.length !== 4 ||
      newPin !== confirmPin
    ) {
      setConfirmError(true);
      return;
    }
    setLoader(true);
    setMessage3("");
    setError3("");
    try {
      const { latitude, longitude } = await getLocation();

      const response = await generatenewTpin({
        user_id: storedUserid,
        genTpin: newPin,
        confirmTpin: confirmPin,
        latitude,
        longitude,
      });
      if (response?.data?.statusCode == "200") {
        getUserTpinStatus("CV");
        setMessage3("New TPIN generated successfully.");
        toast.success("New TPIN generated successfully!");
        handleCloseModal2();
      } else {
        setError3("Failed to generate TPIN.");
        toast.error("Failed to generate TPIN.");
      }
    } catch (error) {
      setError3("Failed to generate TPIN.");
      toast.error("Failed to generate TPIN.");
    } finally {
      setLoader(false);
    }
  };
  const length1 = 6;
  const [otp1, setOtp1] = useState(new Array(length1).fill(""));
  const inputs1 = useRef([]);
  const otpChandleChange1 = (index, value) => {
    const newOtp = [...otp1];
    if (value.length === 2) {
      return;
    }
    newOtp[index] = value;
    setOtp1(newOtp);
    if (value && index < length1 - 1 && inputs1.current[index + 1]) {
      inputs1.current[index + 1].focus();
    }
  };

  const handleKeyDown1 = (index, e) => {
    if (
      e.key === "Backspace" &&
      !otp1[index] &&
      index > 0 &&
      inputs1.current[index - 1]
    ) {
      inputs1.current[index - 1].focus();
    }
  };

  const isAllInputsFilled = !otp1.includes("");

  const handleSubmitOTP = async () => {
    setLoading(true);
    setMessage2("");
    setError2("");

    try {
      const response = await verifyOtpForTpin({
        user_id: storedUserid,
        otp: otp1.join(""),
      });
      // console.log("otpres", response);

      if (response?.data?.statusCode == "200") {
        getUserTpinStatus("OV");
        setMessage2("OTP verified successfully.");
        toast.success("OTP verified successfully.");
        handleSubmitmodal3();
      } else if (response?.message === "Invalid OTP") {
        setError2("Invalid OTP.");
        toast.error("Invalid OTP.");
      } else {
        setError2("Failed to verify OTP.");
        toast.error("Failed to verify OTP.");
      }
    } catch (error) {
      console.error("otperr", error);

      if (error.response?.data?.message === "Invalid OTP") {
        setError2("Invalid OTP.");
        toast.error("Invalid OTP.");
      } else {
        setError2("Failed to verify OTP.");
        toast.error("Failed to verify OTP.");
      }
    } finally {
      setLoading(false);
    }
  };

  const dummyInputs = new Array(length1).fill(null);
  function processCustomerMobile() {
    var value = window.localStorage.getItem("regisNumber");

    var mobile = "";

    if (value) {
      if (value.charAt(0) == "+" || value.charAt(0) == "0") {
        mobile = value.replace(/[^a-zA-Z0-9+]/g, "").substr(3);
      } else {
        mobile = value.replace(/[^a-zA-Z0-9]/g, "");
      }
    } else {
      // console.log("No registration number found in localStorage.");
    }

    return mobile;
  }

  var customerMobile = processCustomerMobile();

  useEffect(() => {
    // console.log(" timeLeft:", timeLeft, "isTimerEnded:", isTimerEnded);
    let timer;
    if (timeLeft > 0 && !isTimerEnded) {
      timer = setTimeout(() => {
        // console.log("Decreasing timeLeft:", timeLeft - 1);
        setTimeLeft((prevTime) => prevTime - 1);
      }, 1000);
    } else if (timeLeft === 0 && !isTimerEnded) {
      setIsTimerEnded(true);
      // console.log("Timer ended");
    }
    return () => clearTimeout(timer);
  }, []);

  const formatTime = (time) => {
    const minutes = Math.floor(time / 60);
    const seconds = time % 60;
    return `${String(minutes).padStart(2, "0")}:${String(seconds).padStart(
      2,
      "0"
    )}`;
  };

  const handleResendOTP = async () => {
    setResendLoading(true);
    setError2("");
    setMessage2("");

    try {
      const response = await generateOtpForResendTpin({
        customer_mobile: customerMobile,
        user_id: storedUserid,
      });
      if (response.status === 200) {
        setMessage2("OTP has been resent successfully.");
        setTimeLeft(180);
        setIsTimerEnded(false);
      } else {
        setError2("Failed to resend OTP.");
      }
    } catch (error) {
      setError2("Failed to resend OTP.");
    } finally {
      setResendLoading(false);
    }
  };
  const storedUserType = localStorage.getItem("userType");
  const userName = walletData ? walletData?.name : "";
  useEffect(() => {
    const fetchData = async () => {
      try {
        const data = await demoapiCheck();
        // console.log("ledgerreport report Service Report Data:", data);
      } catch (error) {
        // console.error("Error fetching commission service report data:", error);
      }
    };
    fetchData();
  }, []);
  return (
    <>
      {modalVisibility.showOverlay && (
        <div className="overlay modalpinoverlayclass"></div>
      )}
      <div className="ContentArea">
        <div className="card overrideCard">
          <CustomMarquee retailerName={userName} />
          <RetailerDashboard
            storedUserType={storedUserType}
            services={services}
            data={data}
            isInstantpaypresnt={isInstantpaypresnt}
          />
          {/* {!(storedUserType === 'Super Distributor' || storedUserType === 'Distributor') && (
                        <>
                            <BBpsservicelist services={services} />
                            {<RetailerDashboard storedUserType={storedUserType} />}
                        </>
                    )} */}
        </div>
      </div>
      {/* Bootstrap Modal 1 */}
      {modalVisibility.showModal && (
        <div
          className={`modal fade mediaqurymodalpop show d-block`}
          tabIndex="-1"
          role="dialog"
          aria-labelledby="exampleModalLabel"
          aria-hidden="true"
          onHide={handleCloseModal1}
        >
          <div className="lock-screen">
            <div className="lock-screen__content">
              <div className="image-bg">
                <img
                  className="lock-screen__imgae"
                  src={image}
                  alt="bankmudraa"
                />
              </div>
              {/* <h1 className="lock-screen__greeting">Hey, {dataprofile?.name?.split(' ')[0] || ""}</h1> */}
              <h2
                className="lock-screen__message mt-3"
                style={{
                  color: "var(--bs-primary2)",
                }}
              >
                Generate Tpin for secure payments.
              </h2>
              <p
                className="lock-screen__instruction"
                style={{
                  color: "var(--bs-primary2)",
                }}
              >
                Click on button to generate Tpin
              </p>
              <button
                className="unlock-button"
                onClick={handleSubmitmodal1}
                style={{
                  color: "var(--bs-primary2)",
                  border: "1px solid var(--bs-primary3)",
                }}
              >
                {loader1 ? (
                  <span
                    className="spinner-border spinner-border-sm"
                    role="status"
                    aria-hidden="true"
                  ></span>
                ) : (
                  "Generate Tpin"
                )}
              </button>

              {message1 && (
                <div
                  className="alert alert-success mt-1"
                  role="alert"
                  style={{
                    color: "var(--bs-primary2)",
                  }}
                >
                  {message1}
                </div>
              )}
              {error1 && (
                <div className="alert alert-danger mt-1" role="alert">
                  {error1}
                </div>
              )}
            </div>
          </div>
        </div>
      )}
      {modalVisibility.showModal3 && (
        <>
          <DashboardOtpModal
            modalVisibility={modalVisibility}
            handleCloseModal3={handleCloseModal3}
            handleSubmitmodal3={handleSubmitmodal3}
            storedUserid={storedUserid}
            setModalVisibility={setModalVisibility}
            getUserTpinStatus={getUserTpinStatus}
          />
        </>
      )}
      {modalVisibility.showModal2 && (
        <div
          className={`modal fade modal-cssclasss mediaqurymodalpop show d-block`}
          tabIndex="-1"
          role="dialog"
        >
          <div className="modal-dialog getpinmodal" role="document">
            <div className="modal-content text-center">
              <div className="modal-header text-center">
                <h5 className="modal-title modaltitle-otp">Create your Tpin</h5>
              </div>
              <div className="modal-body">
                <div className="col-lg-12">
                  <form onSubmit={handleSubmit}>
                    <div className="mb-3">
                      <div className="input-group">
                        <input
                          type={showNewPin ? "text" : "password"}
                          className={`form-control ${
                            newPin.length === 4 && !pinError ? "is-valid" : ""
                          }`}
                          id="newPin"
                          value={newPin}
                          onChange={handleNewPinChange}
                          placeholder="Enter new PIN"
                          aria-label="New PIN"
                          aria-describedby="basic-addon1"
                          required
                        />
                        {newPin.length === 4 && !pinError && (
                          <span className="position-absolute top-50 end-0 translate-middle-y pe-2">
                            <TiTick />
                          </span>
                        )}
                        <button
                          className="btn "
                          style={{ borderColor: "#aaaaaa57" }}
                          type="button"
                          onClick={() => setShowNewPin(!showNewPin)}
                        >
                          {showNewPin ? <FaEyeSlash /> : <FaEye />}
                        </button>
                      </div>
                      {pinError && (
                        <div className="text-danger">
                          Please enter exactly 4 numeric digits.
                        </div>
                      )}
                    </div>
                    <div className="mb-3">
                      <div className="input-group">
                        <input
                          type={showConfirmPin ? "text" : "password"}
                          className={`form-control ${
                            confirmPin.length === 4 && !confirmError
                              ? "is-valid"
                              : ""
                          }`}
                          id="confirmPin"
                          value={confirmPin}
                          onChange={handleConfirmPinChange}
                          placeholder="Confirm new PIN"
                          aria-label="Confirm PIN"
                          aria-describedby="basic-addon2"
                          required
                        />

                        <button
                          className="btn "
                          style={{ borderColor: "#aaaaaa57" }}
                          type="button"
                          onClick={() => setShowConfirmPin(!showConfirmPin)}
                        >
                          {showConfirmPin ? <FaEyeSlash /> : <FaEye />}
                        </button>
                      </div>
                      {confirmError && (
                        <div className="text-danger">
                          PINs do not match or are not 4 digits.
                        </div>
                      )}
                    </div>
                    <button
                      type="submit"
                      className="btn btn-submit-otp"
                      disabled={
                        pinError ||
                        confirmError ||
                        newPin.length !== 4 ||
                        confirmPin.length !== 4
                      }
                    >
                      {loader ? (
                        <span
                          className="spinner-border spinner-border-sm"
                          role="status"
                          aria-hidden="true"
                        ></span>
                      ) : (
                        "Submit"
                      )}
                    </button>
                    {message3 && (
                      <div className="alert alert-success mt-3">{message3}</div>
                    )}
                    {error3 && (
                      <div className="alert alert-danger mt-3">{error3}</div>
                    )}
                  </form>
                </div>
              </div>
            </div>
          </div>
        </div>
      )}
    </>
  );
}
export default DasBoardCheck;
