import React, { useEffect, useState } from 'react';
import { Tabs, Tab, Card, CardContent, Typography, Box, Container, useMediaQuery } from '@mui/material';
import { styled } from '@mui/system';
import { LocalOffer, Receipt } from '@mui/icons-material';
import { Row, Col } from 'react-bootstrap';
import ServiceGrid from './ServiceGrid';
import Todaybusiness from './Todaybusiness';
import "./Tabstyle.css"
import Dashboardcard from './Dashboardcard';
import filterApiUser from '../../../../utils/apiuserfilter';
// Custom theme values
// Custom Theme (only background color kept)
const customTheme = {
  colors: {
    background: 'var(--bs-primary3)', // only this color remains
  },
  fontWeights: {
    regular: 400,
    medium: 500,
    bold: 700,
  },
  fontSize: {
    small: '14px',
    medium: '16px',
    large: '20px',
  },
  spacing: (factor) => `${8 * factor}px`,
  breakpoints: {
    sm: '600px',
  },
  shadows: {
    medium: '0 4px 6px rgba(0, 0, 0, 0.1)',
    large: '0 10px 15px rgba(0, 0, 0, 0.1)',
  },
};

// Styled Tabs with background color only
const StyledTabs = styled(Tabs)({
  display: 'none',
  '& .MuiTabs-indicator': {
    backgroundColor: customTheme.colors.background,
    height: 3,
    display: 'none',
  },
});

// Styled Tab with font styling, no color styling
const StyledTab = styled(Tab)({
  textTransform: 'none',
  fontWeight: customTheme.fontWeights.medium,
  fontSize: customTheme.fontSize.medium,
  marginRight: customTheme.spacing(4),
  [`@media (max-width: ${customTheme.breakpoints.sm})`]: {
    fontSize: customTheme.fontSize.small,
    marginRight: customTheme.spacing(2),
  },
});

// TabPanel with padding only
const TabPanel = styled(Box)({
  padding: customTheme.spacing(3),
  [`@media (max-width: ${customTheme.breakpoints.sm})`]: {
    padding: customTheme.spacing(2),
  },
});

const RetailerDashboard = ({ storedUserType, services, data, isInstantpaypresnt }) => {
  const [tabValue, setTabValue] = useState(null);
  const isMobile = useMediaQuery(`(max-width: ${customTheme.breakpoints.sm})`);
  useEffect(() => {
    if (storedUserType === 'zsm' || storedUserType === 'asm' || storedUserType === 'Super Distributor' || storedUserType === 'Distributor') {
      setTabValue(1)
    }
    else {
      setTabValue(0)
    }
  }, [storedUserType])

  const handleTabChange = (event, newValue) => {
    setTabValue(newValue);
  };



  if (storedUserType != 'Retailer' && tabValue === 1) {
    return (
      <Box sx={{ p: customTheme.spacing(3) }}>
        {/* <Typography variant="h4" sx={{ color: customTheme.colors.text, fontWeight: customTheme.fontWeights.bold }}>
          Transactions
        </Typography> */}
        {/* <Dashboardcard data={data}/> */}
        <Todaybusiness data={data} />
      </Box>
    );
  }

  return (
    <Container sx={{ p: 0, backgroundColor: "var(--bs-primary1)" }}>
      <StyledTabs
        value={tabValue}
        onChange={handleTabChange}
        aria-label="dashboard tabs"
        variant={isMobile ? "fullWidth" : "standard"}
        centered={!isMobile}
      >
        <StyledTab icon={<LocalOffer />} label="Services" />
        {filterApiUser(<StyledTab icon={<Receipt />} label="Transactions" />)}
      </StyledTabs>
      <TabPanel sx={{ p: 0 }}>
        {tabValue === 0 && <ServiceGrid services={services} isInstantpaypresnt={isInstantpaypresnt} />}
        {tabValue === 1 && (
          <Box>
            {/* <Typography variant="h4" gutterBottom sx={{ color: customTheme.colors.main, fontWeight: customTheme.fontWeights.bold }}>
              Transaction Page
            </Typography> */}
            <Todaybusiness data={data} />
          </Box>
        )}
      </TabPanel>
    </Container>
  );
};

