import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import ReactFancyBox from "react-fancybox";
import "react-fancybox/lib/fancybox.css";
import {
  Grid,
  Paper,
  Typography,
  Box,
  TextField,
  useMediaQuery,
  Alert,
  Card,
  CardMedia,
  CardContent,
} from "@mui/material";
import { useTheme, styled } from "@mui/material/styles";
import {
  Payment,
  CreditCard,
  ElectricBolt,
  LocalGasStation,
  PhoneAndroid,
  Tv,
  Wifi,
  LocalShipping,
  School,
  LocalHospital,
  Build,
  ErrorOutline,
} from "@mui/icons-material";

import { InputAdornment } from "@mui/material";
import SearchIcon from "@mui/icons-material/Search";
import { motion } from "framer-motion";
import { genrateUrl } from "../../../../utils/genrateUrl";
import { mobileNumberFilter } from "../../../../baseUrl";
import UserGuard from "../../../../utils/UserGuard";
const placeholderTexts = [
  "Search by name",
  "Search by name , Bank Transfer",
  "Search by name , Bank Transfer , Aadhaar withdraw...",
];
// Professional color palette
const colors = {
  primary: "#1976d2",
  background: "var(--bs-secondary2) !important",
  cardBg: "#ffffff",
  text: "var(--bs-nav)",
  iconBg: "var(--bs-primary1)",
  error: "#f44336",
  warning: "#ff9800",
};
const ServiceItem = styled(Paper)(({ theme }) => ({
  padding: theme.spacing(2),
  textAlign: "center",
  backgroundColor: colors.cardBg,
  display: "flex",
  flexDirection: "column",
  alignItems: "center",
  justifyContent: "center",
  cursor: "pointer",
  transition: "all 0.3s ease",
  borderRadius: "8px",
  border: "1px solid #e0e0e0",
  height: "120px",
  position: "relative", // Required for background-clip approach
  zIndex: 0,
  // overflow: 'hidden', // Ensures content stays clipped to the border radius
  "&:hover": {
    transform: "translateY(-5px)",
    // boxShadow: '0 4px 20px rgba(0,0,0,0.1)',
    boxShadow: `rgba(0, 0, 0, 0.35) 0px 2px 8px`,

    // border: '1px solid #FFCC29',
    // backgroundImage: '-webkit-linear-gradient(right, #F87A1F, #FF5733, #D94615, #FFA07A)',
    // backgroundColor:'var(--bs-secondary2) !important',
    backgroundClip: "border-box",
  },
}));
const IconWrapper = styled(Box)(({ theme }) => ({
  backgroundColor: colors.iconBg,
  borderRadius: "50%",
  padding: theme.spacing(1.5),
  marginBottom: theme.spacing(1.5),
  display: "flex",
  justifyContent: "center",
  alignItems: "center",
  position: "absolute",
  top: "-30px",
  zIndex: 10,
  width: "48px",
  height: "48px",
  [theme.breakpoints.up("sm")]: {
    width: "56px",
    height: "56px",
  },
}));
const ErrorMessage = styled(Alert)(({ theme }) => ({
  margin: theme.spacing(2),
  width: "100%",
}));
function ServiceGrid({ services = [], isInstantpaypresnt }) {
  const navigate = useNavigate();
  const userNumber = localStorage.getItem("regisNumber");
  const [searchTerm, setSearchTerm] = useState("");
  const [focused, setFocused] = useState(false);
  const [error, setError] = useState(null);
  const [filteredServices, setFilteredServices] = useState([]);
  const theme = useTheme();
  const isXs = useMediaQuery(theme.breakpoints.only("xs"));
  useEffect(() => {
    try {
      if (!Array.isArray(services)) {
        throw new Error("Invalid services data received");
      }
      setError(null);
    } catch (err) {
      setError(err);
      console.error("ServiceGrid initialization error:", err);
    }
  }, [services]);
  const [placeholderIndex, setPlaceholderIndex] = useState(0);
  useEffect(() => {
    const interval = setInterval(() => {
      setPlaceholderIndex((prev) => (prev + 1) % placeholderTexts.length);
    }, 1000);
    return () => clearInterval(interval);
  }, []);
  const routeMap = {
    206: "/aeps-cash-deposit",
    19: "/ccbill_payment_report",
    18: "/electricity-bills",
    33: "/mobile-reacharge",
    13: "/dth-reacharge",
    11: "/gas-bills",
    10: "/postpaid-recharge",
    5: "/Boardband",
    35: "/airtel-cms",
    12: "/landline-bills",
    208: "/aeps-OnBoarded",
    207: "/aeps-OnBoarded",
    9: "/fastag-recharge",
    188: "/money-transfer",
    28: "/water-bills",
    15: "/hospital-pathology",
    3: "/educational-fees",
    16: "/lpg-gasbill-payment",
    31: "/nmrc-paymentbill",
    36: "/quickdhan-payment",
    8: "/insurance-payment",
    17: "/muncipaltax-paymentbill",
    29: "/metro-recharge",
    2: "/loan-repayment",
    22: "/life-insurance",
    4: "/housing-society",
    14: "/health-insurance-bill",
    26: "/health-insurance",
    30: "/clubs-associations",
    21: "/cable-tv",
    24: "/b2b",
    6: "/subscription",
    25: "/rent-bill",
    216: "/recurringdeposite",
    7: "/muncipaltax-paymentbill",
    1: "/hospital-pathology",
    27: "/donation",
  };

  const handleNavigation = (route) => {
    try {
      // const route = routeMap[serviceId] || "/Dashboard";
      navigate(route);
    } catch (error) {
      // console.error('Navigation error:', error);
      // setError(error);
      // navigate("/Dashboard");
    }
  };
  useEffect(() => {
    try {
      if (!Array.isArray(services)) {
        setFilteredServices([]);
        return;
      }

      const filtered = services.filter((service) => {
        try {
          if (!service || typeof service.service_name === "undefined") {
            return false;
          }

          const serviceName = String(service.service_name || "");
          const search = String(searchTerm || "");

          return (
            serviceName.localeCompare(search, undefined, {
              sensitivity: "base",
            }) === 0 || serviceName.includes(search)
          );
        } catch (err) {
          console.error("Service filtering error:", err);
          return false;
        }
      });

      setFilteredServices(filtered);
    } catch (err) {
      console.error("Filtering error:", err);
      setFilteredServices([]);
    }
  }, [services, searchTerm]);
  const storedUserType = localStorage.getItem("userType");
  // If there's a critical error, show the error message
  if (error) {
    return (
      <ErrorMessage severity="error">
        <Typography variant="h6">Error Loading Services</Typography>
        <Typography variant="body2">{error.message}</Typography>
      </ErrorMessage>
    );
  }
  // If services is not an array, show a warning
  if (!Array.isArray(services)) {
    return (
      <ErrorMessage severity="warning">
        No services available at the moment. Please try again later.
      </ErrorMessage>
    );
  }
  // backgroundColor: colors.background,
  return (
    <Box
      sx={{
        paddingY: { xs: 2, sm: 3, md: 4 },
        backgroundColor: "",
        paddingX: 0,
        borderRadius: "12px",
      }}
    >
      <UserGuard
        currentUser={storedUserType}
        excludeUserList={["Reseller", "reseller partner api user"]}
      >
        {mobileNumberFilter?.includes(userNumber) !== true && (
          <Box
            sx={{
              display: "flex",
              flexDirection: {
                xs: "column-reverse",
                sm: "column-reverse",
                md: "column-reverse",
                lg: "row",
              },
              alignItems: "center",
              gap: 2,
              width: "100%",
              bgcolor: "var(--bs-primary1)",
              color: "var(--bs-primary2)",
              p: 2,
              mb: 2,
              borderRadius: 2,
              border: "2px solid var(--bs-primary3)",
            }}
          >
            <motion.div
              style={{
                width: "100%",
                flex: { md: "0 0 50%", lg: "0 0 50%" },
              }}
            >
              <TextField
                variant="outlined"
                placeholder={placeholderTexts[placeholderIndex]}
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                fullWidth
                size="small"
                sx={{
                  width: { xs: "100%", sm: "100%", md: "100%", lg: "100%" },
                  bgcolor: "var(--bs-primary1)",
                  color: "var(--bs-primary2)",
                  borderRadius: 1,
                  "& .MuiOutlinedInput-root": {
                    color: "var(--bs-primary2)",
                    "& fieldset": {
                      // borderColor: "var(--bs-primary3)",
                      borderWidth: "2px",
                    },
                    "&:hover fieldset": {
                      borderColor: "#c4c4c4",
                      borderWidth: "2px",
                    },
                    "&.Mui-focused fieldset": {
                      borderColor: "#c4c4c4",
                      borderWidth: "2px",
                    },
                  },
                  input: { color: "var(--bs-primary2)" },
                }}
                InputProps={{
                  startAdornment: (
                    <InputAdornment position="start">
                      <SearchIcon
                        sx={{ cursor: "pointer", color: "var(--bs-primary2)" }}
                      />
                    </InputAdornment>
                  ),
                }}
              />
            </motion.div>

            <Typography
              sx={{
                flex: { md: "0 0 50%", lg: "0 0 50%" },
                width: "100%",
                overflow: "hidden",
                whiteSpace: "nowrap",
                position: "relative",
                fontWeight: 600,
                color: "var(--bs-primary2)",
                textAlign: "center",
                fontSize: {
                  xs: "14px",
                  sm: "15px",
                  md: "18px",
                  lg: "18px",
                },

                // Desktop marquee span
                "& .desktopMarquee": {
                  display: { xs: "none", md: "inline-block" },
                  animation: "marquee 15s linear infinite",
                },

                // Mobile static span
                "& .mobileText": {
                  display: { xs: "inline-block", md: "none" },
                },

                "@keyframes marquee": {
                  "0%": { transform: "translateX(100%)" },
                  "100%": { transform: "translateX(-100%)" },
                },
              }}
            >
              {/* Desktop (scrolling) */}
              <span className="desktopMarquee">
                {greeting}, Welcome to Bank Mudraa
              </span>

              {/* Mobile (static) */}
              <span className="mobileText">
                {greeting}, Welcome to Bank Mudraa
              </span>
            </Typography>
          </Box>
        )}
      </UserGuard>
      {filteredServices.length === 0 && searchTerm && (
        <Alert severity="error" sx={{ mb: 2 }}>
          No services found matching your search.
        </Alert>
      )}
      <Grid container spacing={2}>
        {filteredServices.map((service) => {
          try {
            const serviceId = service?._id || "";
            const serviceName = service?.service_name || "Unnamed Service";
            const serviceIcon = service?.icon || "";
            const pathName = service?.route || "#";
            return (
              <Grid item xs={6} sm={4} md={3} lg={2} key={serviceId}>
                <Link to={pathName}>
                  <Card
                    className="boxModel"
                    sx={{
                      height: "100%",
                      borderRadius: "32px",
                      background: "linear-gradient(145deg, #f8f9fa 0%, #e9ecef 100%)",
                      padding: "24px 16px",
                      position: "relative",
                      overflow: "hidden",
                      display: "flex",
                      gap: "6px",
                      // flexDirection: "column",
                      alignItems: "center",
                      textAlign: "center",
                      // padding: 2,
                       border: "none",
                      boxShadow: "0 0 0 0.5px var(--bs-border-primary1)",
                      transition: "all 0.4s cubic-bezier(0.4, 0, 0.2, 1)",
                      // borderRadius: 2,
                      // outlineColor : "var(--bs-border-primary1)",
                      // outline: "0.5px solid var(--bs-border-primary1) !important",
                      // outlineOffset: "2px",
                      // transition: "transform 0.2s ease-in-out",
                      // "&:hover": {
                      //   transform: "scale(1.03)",
                      // },
                       /*  LARGE BACKGROUND ICON - More visible like the reference image */
                      "&::before": {
                        content: '""',
                        position: "absolute",
                        top: "50%",
                        left: "50%",
                        transform: "translate(-50%, -50%)",
                        width: "180px",
                        height: "180px",
                        backgroundImage: `url(${serviceIcon})`,
                        backgroundSize: "contain",
                        backgroundPosition: "center",
                        backgroundRepeat: "no-repeat",
                        opacity: 0.06,
                        filter: "grayscale(20%)",
                        zIndex: 0,
                        transition: "all 0.4s cubic-bezier(0.4, 0, 0.2, 1)",
                      },

                      /*  PREMIUM GRADIENT OVERLAY */
                      "&::after": {
                        content: '""',
                        position: "absolute",
                        inset: 0,
                        background: "radial-gradient(circle at top right, rgba(255,255,255,0.8) 0%, transparent 70%)",
                        borderRadius: "32px",
                        zIndex: 1,
                        opacity: 0,
                        transition: "opacity 0.4s ease",
                      },

                      "&:hover": {
                        transform: "translateY(-8px) scale(1.02)",
                        boxShadow: "0 12px 32px rgba(0, 0, 0, 0.12)",
                        background: "linear-gradient(145deg, #ffffff 0%, #f1f3f5 100%)",
                        
                        "&::before": {
                          width: "200px",
                          height: "200px",
                          opacity: 0.12,
                          transform: "translate(-50%, -50%) rotate(12deg) scale(1.1)",
                        },

                        "&::after": {
                          opacity: 1,
                        },

                        "& .MuiCardMedia-root": {
                          transform: "scale(1.15) translateY(-2px)",
                          filter: "drop-shadow(0 4px 8px rgba(0,0,0,0.15))",
                        },

                        "& .service-title": {
                          color: "#1a1a1a",
                          fontWeight: 700,
                        },
                      },

                      "@media (hover: none)": {
                        "&:active": {
                          transform: "scale(0.98)",
                        },
                      },
                    }}
                  >
                    <CardMedia
                      component="img"
                      src={serviceIcon}
                      alt={serviceName}
                      loading="eager"
                      onError={(e) => {
                        e.target.onerror = null;
                        e.target.src = "/servicesicon.png";
                      }}
                      sx={{
                        width: { xs: 40, sm: 50 },
                        height: { xs: 40, sm: 50 },
                        objectFit: "contain",
                        marginBottom: 2,
                        position: "relative",
                        gap: "8px",
                        zIndex: 2,
                        filter: "drop-shadow(0 2px 4px rgba(0,0,0,0.08))",
                        transition: "all 0.4s cubic-bezier(0.4, 0, 0.2, 1)",
                      }}
                    />
                    <CardContent sx={{ padding: 0, position: "relative", zIndex: 2 }}>
                      <Typography variant="body2"  sx={{
                          fontSize: { xs: "13px", sm: "14px" },
                          fontWeight: 600,
                          textAlign: "center",
                          lineHeight: 1.3,
                          color: "#2c3e50",
                          // display: "-webkit-box",
                          WebkitLineClamp: 2,
                          WebkitBoxOrient: "vertical",
                          overflow: "hidden",
                          textOverflow: "ellipsis",
                          wordBreak: "break-word",
                          letterSpacing: "0.01em",
                          transition: "all 0.3s ease",
                        }}>
                        {serviceName}
                      </Typography>
                    </CardContent>
                  </Card>
                </Link>
              </Grid>
            );
          } catch (error) {
            console.error("Service rendering error:", error);
            return null;
          }
        })}
      </Grid>
    </Box>
  );
}

export default ServiceGrid;

// ------ Dynamic Greeting ------
const getGreeting = () => {
  const hour = new Date().getHours();

  if (hour >= 5 && hour < 12) return "Good Morning";
  if (hour >= 12 && hour < 17) return "Good Afternoon";
  if (hour >= 17 && hour < 22) return "Good Evening";

  return "Welcome to Bank Mudraa";
};

const greeting = getGreeting();
