import {
  Avatar,
  Grid,
  Stack,
  Typography,
  Box,
  Button,
  Divider,
  IconButton,
  Card,
  CardContent,
  Chip,
} from "@mui/material";
import DasBoardCheck from "../../../components/admin/daseboardCheck/DasBoardChaeck";
import { genrateUrl } from "../../../utils/genrateUrl";
import ProfileModal from "./ProfileModal";
import { useState, useEffect, useRef } from "react";
import useWalletReport from "../../../hooks/useWalletReport";
import { useNavigate } from "react-router-dom";
import Transactions from "./Transactions";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import VisibilityIcon from "@mui/icons-material/Visibility";
import VisibilityOffIcon from "@mui/icons-material/VisibilityOff";
import AccountBalanceWalletIcon from "@mui/icons-material/AccountBalanceWallet";
import SupportAgentIcon from "@mui/icons-material/SupportAgent";
import ReceiptLongIcon from "@mui/icons-material/ReceiptLong";

function DaseboardCheckPage({
  profiledata,
  walletData,
  services,
  position,
  isInstantpaypresnt,
}) {
  const navigate = useNavigate();
  const [showModal, setShowModal] = useState(false);
  const [showBalance, setShowBalance] = useState(false);
  const [sidebarOpen, setSidebarOpen] = useState(true);
  const autoCloseRef = useRef(null);
  const userInteractedRef = useRef(false);
  const { data } = useWalletReport();

  useEffect(() => {
    autoCloseRef.current = setTimeout(() => {
      if (!userInteractedRef.current) {
        setSidebarOpen(false);
      }
    }, 3000);

    return () => {
      clearTimeout(autoCloseRef.current);
    };
  }, []);
  const handleToggleSidebar = () => {
    userInteractedRef.current = true;
    if (autoCloseRef.current) {
      clearTimeout(autoCloseRef.current);
      autoCloseRef.current = null;
    }
    setSidebarOpen((prev) => !prev);
  };

  return (
    <Grid
      container
      sx={{
        height: "100%",
        overflow: "auto",
        backgroundColor: "var(--bs-primary3)",
        position: "relative",
      }}
    >
      {/* Left scrollable section */}
      <Grid
        item
        xs={12}
        md={sidebarOpen ? 10.2 : 12}
        sx={{
          height: "100%",
          overflowY: "auto",
          pr: { xs: 0, md: 2 },
          scrollbarWidth: "none",
          "&::-webkit-scrollbar": { display: "none" },
          transition: "all 0.65s cubic-bezier(0.4, 0, 0.2, 1)",
        }}
      >
        <DasBoardCheck
          walletData={walletData}
          services={services}
          position={position}
          isInstantpaypresnt={isInstantpaypresnt}
        />
      </Grid>
      {/* Mobile view - Original sidebar (only visible on small screens) */}
      <Grid
        item
        xs={12}
        sx={{
          display: { xs: "flex", md: "none" },
          flexDirection: "column",
          alignItems: "center",
          gap: 2,
          mt: 2,
          px: 2,
          pb: 2,
        }}
      >
        {/* PROFILE SECTION - Mobile */}
        <Card
          sx={{
            width: "100%",
            color: "var(--bs-primary2)",
            background:
              "linear-gradient(135deg, var(--bs-primary1) 0%, var(--bs-primary1) 100%)",
            borderRadius: "20px",
            boxShadow: "0 8px 24px rgba(0,0,0,0.12)",
            border: "1px solid var(--bs-primary3)",
            position: "relative",
            overflow: "visible",
          }}
        >
          <CardContent sx={{ p: 2.5, "&:last-child": { pb: 2.5 } }}>
            <Stack direction="row" spacing={2} alignItems="center">
              <Box
                sx={{
                  position: "relative",
                  "&::after": {
                    content: '""',
                    position: "absolute",
                    inset: -3,
                    borderRadius: "50%",
                    padding: "3px",
                    background: "linear-gradient(135deg, #667eea, #764ba2)",
                    WebkitMask:
                      "linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0)",
                    WebkitMaskComposite: "xor",
                    maskComposite: "exclude",
                  },
                }}
              >
                <Avatar
                  src={
                    profiledata?.profile
                      ? profiledata?.profile
                      : `https://ui-avatars.com/api/?name=${
                          profiledata?.pan_name || profiledata?.name
                        }&background=random&color=fff&w=50`
                  }
                  sx={{
                    width: 60,
                    height: 60,
                    border: "2px solid var(--bs-primary3)",
                  }}
                />
              </Box>
              <Box sx={{ flex: 1, minWidth: 0 }}>
                <Typography
                  variant="h6"
                  sx={{
                    color: "var(--bs-primary2)",
                    fontWeight: 600,
                    fontSize: "16px",
                    mb: 0.5,
                    whiteSpace: "nowrap",
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                  }}
                >
                  {profiledata?.name || ""}
                </Typography>
                <Chip
                  icon={<AccountBalanceWalletIcon sx={{ fontSize: 16 }} />}
                  label={`${
                    showBalance ? profiledata?.main_wallet || 0 : ""
                  }`}
                  size="small"
                  sx={{
                    backgroundColor: "var(--bs-primary3)",
                    color: "var(--bs-primary2)",
                    fontWeight: 600,
                    fontSize: "13px",
                    height: "26px",
                  }}
                />
              </Box>
              <IconButton
                size="small"
                onClick={() => setShowBalance(!showBalance)}
                sx={{
                  backgroundColor: "var(--bs-primary3)",
                  color: "var(--bs-primary2)",
                  width: 32,
                  height: 32,
                  "&:hover": {
                    backgroundColor: "var(--bs-primary3)",
                    opacity: 0.8,
                  },
                }}
              >
                {showBalance ? (
                  <VisibilityOffIcon sx={{ fontSize: 18 }} />
                ) : (
                  <VisibilityIcon sx={{ fontSize: 18 }} />
                )}
              </IconButton>
            </Stack>
            <Button
              variant="contained"
              fullWidth
              onClick={() => setShowModal(true)}
              sx={{
                mt: 2,
                background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                color: "white",
                textTransform: "none",
                fontWeight: 600,
                fontSize: "14px",
                borderRadius: "10px",
                py: 1,
                boxShadow: "0 4px 12px rgba(102, 126, 234, 0.3)",
                "&:hover": {
                  background:
                    "linear-gradient(135deg, #764ba2 0%, #667eea 100%)",
                  boxShadow: "0 6px 16px rgba(102, 126, 234, 0.4)",
                },
              }}
            >
              View Profile
            </Button>
          </CardContent>
        </Card>

        {/* HELP CENTER SECTION - Mobile */}
        <Card
          sx={{
            width: "100%",
            background: "var(--bs-primary1)",
            borderRadius: "20px",
            boxShadow: "0 8px 24px rgba(0,0,0,0.12)",
            border: "1px solid var(--bs-primary3)",
          }}
        >
          <CardContent sx={{ p: 2, "&:last-child": { pb: 2 } }}>
            <Stack direction="row" spacing={1.5} alignItems="center" mb={1.5}>
              <Box
                sx={{
                  width: 36,
                  height: 36,
                  borderRadius: "10px",
                  background:
                    "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <SupportAgentIcon sx={{ color: "white", fontSize: 20 }} />
              </Box>
              <Typography
                variant="h6"
                sx={{
                  color: "var(--bs-primary2)",
                  fontWeight: 600,
                  fontSize: "15px",
                }}
              >
                Help Center
              </Typography>
            </Stack>

            <Stack spacing={1}>
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                  gap: 1,
                  p: 1,
                  borderRadius: "10px",
                  backgroundColor: "var(--bs-primary3)",
                }}
              >
                <Typography sx={{ fontSize: "16px", flexShrink: 0 }}>
                  
                </Typography>
                <Typography
                  variant="body2"
                  sx={{
                    fontSize: "13px",
                    color: "var(--bs-primary2)",
                    fontWeight: 500,
                  }}
                >
                  +91 1242262222
                </Typography>
              </Box>

              <Box
                sx={{
                  display: "flex",
                  alignItems: "flex-start",
                  gap: 1,
                  p: 1,
                  borderRadius: "10px",
                  backgroundColor: "var(--bs-primary3)",
                }}
              >
                <Typography sx={{ fontSize: "16px", flexShrink: 0, mt: 0.2 }}>
                  
                </Typography>
                <Typography
                  variant="body2"
                  sx={{
                    fontSize: "13px",
                    color: "var(--bs-primary2)",
                    fontWeight: 500,
                    wordBreak: "break-word",
                    lineHeight: 1.4,
                  }}
                >
                  support@bankmudraa.com
                </Typography>
              </Box>
            </Stack>

            <Button
              variant="outlined"
              fullWidth
              onClick={() =>
                (window.location.href =
                  "mailto:support@bankmudraa.com?subject=Support Request&body=Hello BankMudraa Support,")
              }
              sx={{
                mt: 1.5,
                color: "var(--bs-primary2)",
                borderColor: "var(--bs-primary3)",
                textTransform: "none",
                fontWeight: 600,
                fontSize: "13px",
                borderRadius: "10px",
                py: 0.8,
                "&:hover": {
                  borderColor: "var(--bs-primary2)",
                  backgroundColor: "transparent",
                },
              }}
            >
              Contact Us
            </Button>
          </CardContent>
        </Card>

        {/* TRANSACTIONS CARD - Mobile */}
        <Card
          sx={{
            width: "100%",
            background: "var(--bs-primary1)",
            borderRadius: "20px",
            boxShadow: "0 8px 24px rgba(0,0,0,0.12)",
            border: "1px solid var(--bs-primary3)",
          }}
        >
          <CardContent sx={{ p: 2.5, "&:last-child": { pb: 2.5 } }}>
            <Stack direction="row" spacing={1.5} alignItems="center" mb={2}>
              <Box
                sx={{
                  width: 40,
                  height: 40,
                  borderRadius: "12px",
                  background:
                    "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <ReceiptLongIcon sx={{ color: "white", fontSize: 22 }} />
              </Box>
              <Typography
                variant="h6"
                sx={{
                  color: "var(--bs-primary2)",
                  fontWeight: 600,
                  fontSize: "16px",
                }}
              >
                Recent Transactions
              </Typography>
            </Stack>

            <Box
              sx={{
                maxHeight: "220px",
                overflowY: "auto",
                overflowX: "hidden",
                mb: 1.5,
                pr: 0.5,
                scrollbarWidth: "thin",
                "&::-webkit-scrollbar": {
                  width: "6px",
                },
                "&::-webkit-scrollbar-track": {
                  background: "var(--bs-primary3)",
                  borderRadius: "4px",
                },
                "&::-webkit-scrollbar-thumb": {
                  background:
                    "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                  borderRadius: "4px",
                  "&:hover": {
                    background:
                      "linear-gradient(135deg, #764ba2 0%, #667eea 100%)",
                  },
                },
              }}
            >
              <Transactions data={data} />
            </Box>

            <Button
              variant="text"
              fullWidth
              onClick={() => navigate("/wallet-report")}
              sx={{
                textTransform: "none",
                fontSize: "14px",
                fontWeight: 600,
                color: "var(--bs-primary2)",
                "&:hover": {
                  backgroundColor: "transparent",
                  textDecoration: "underline",
                },
              }}
            >
              View All Transactions 
            </Button>
          </CardContent>
        </Card>
      </Grid>

      {/* Toggle Button - Only for desktop */}
      <Box
        sx={{
          display: { xs: "none", md: "block" },
          position: "fixed",
          right: sidebarOpen ? "335px" : "10px",
          top: "50%",
          transform: "translateY(-50%)",
          zIndex: 1300,
          transition: "all 0.65s cubic-bezier(0.4, 0, 0.2, 1)",
        }}
      >
        <IconButton
          onClick={handleToggleSidebar}
          sx={{
            backgroundColor: "var(--bs-primary1)",
            color: "var(--bs-primary2)",
            boxShadow: "0 4px 12px rgba(0,0,0,0.15)",
            width: "44px",
            height: "44px",
            "&:hover": {
              backgroundColor: "var(--bs-primary1)",
              boxShadow: "0 6px 16px rgba(0,0,0,0.2)",
            },
            border: "2px solid var(--bs-primary3)",
          }}
        >
          {sidebarOpen ? <ChevronRightIcon /> : <ChevronLeftIcon />}
        </IconButton>
      </Box>
      {/* Right fixed sidebar */}
      <Box
        sx={{
          display: { xs: "none", md: "flex" },
          position: "fixed",
          right: sidebarOpen ? 0 : "-330px",
          top: 0,
          height: "100%",
          width: "330px",
          flexDirection: "column",
          alignItems: "center",
          gap: 2,
          overflowY: "auto",
          overflowX: "hidden",
          backgroundColor: "var(--bs-primary3)",
          borderTopLeftRadius: "32px",
          borderBottomLeftRadius: "8px",
          borderTopRightRadius: "0px",
          borderBottomRightRadius: "0px",
          transition: "all 0.65s cubic-bezier(0.4, 0, 0.2, 1)",
          zIndex: 1200,
          pt: 2,
          pb: 2,
          px: 1.5,
          boxShadow: sidebarOpen ? "-4px 0 20px rgba(0,0,0,0.1)" : "none",
          scrollbarWidth: "thin",
          "&::-webkit-scrollbar": {
            width: "5px",
          },
          "&::-webkit-scrollbar-track": {
            background: "transparent",
          },
          "&::-webkit-scrollbar-thumb": {
            background: "var(--bs-primary3)",
            borderRadius: "4px",
          },
        }}
        onWheel={() => {
          userInteractedRef.current = true;
          if (autoCloseRef.current) {
            clearTimeout(autoCloseRef.current);
            autoCloseRef.current = null;
          }
        }}
        onMouseEnter={() => {
          userInteractedRef.current = true;
          if (autoCloseRef.current) {
            clearTimeout(autoCloseRef.current);
            autoCloseRef.current = null;
          }
        }}
      >
        {/* PROFILE SECTION */}
        <Card
          sx={{
            width: "100%",
            background:
              "linear-gradient(135deg, var(--bs-primary1) 0%, var(--bs-primary1) 100%)",
            borderRadius: "20px",
            boxShadow: "0 8px 24px rgba(0,0,0,0.12)",
            border: "1px solid var(--bs-primary3)",
            position: "relative",
            overflow: "visible",
          }}
        >
          <CardContent sx={{ p: 2.5, "&:last-child": { pb: 2.5 } }}>
            <Stack direction="row" spacing={2} alignItems="center">
              <Box
                sx={{
                  position: "relative",
                  "&::after": {
                    content: '""',
                    position: "absolute",
                    inset: -3,
                    borderRadius: "50%",
                    padding: "3px",
                    background: "linear-gradient(135deg, #667eea, #764ba2)",
                    WebkitMask:
                      "linear-gradient(#fff 0 0) content-box, linear-gradient(#fff 0 0)",
                    WebkitMaskComposite: "xor",
                    maskComposite: "exclude",
                  },
                }}
              >
                <Avatar
                  src={
                    profiledata?.profile
                      ? profiledata?.profile
                      : `https://ui-avatars.com/api/?name=${
                          profiledata?.pan_name || profiledata?.name
                        }&background=random&color=fff&w=50`
                  }
                  sx={{
                    width: 60,
                    height: 60,
                    border: "2px solid var(--bs-primary3)",
                  }}
                />
              </Box>
              <Box sx={{ flex: 1, minWidth: 0 }}>
                <Typography
                  variant="h6"
                  sx={{
                    color: "var(--bs-primary2)",
                    fontWeight: 600,
                    fontSize: "16px",
                    mb: 0.5,
                    whiteSpace: "nowrap",
                    overflow: "hidden",
                    textOverflow: "ellipsis",
                  }}
                >
                  {profiledata?.name || ""}
                </Typography>
                <Chip
                  icon={<AccountBalanceWalletIcon sx={{ fontSize: 16 }} />}
                  label={`${
                    showBalance ? profiledata?.main_wallet || 0 : ""
                  }`}
                  size="small"
                  sx={{
                    backgroundColor: "var(--bs-primary3)",
                    color: "var(--bs-primary2)",
                    fontWeight: 600,
                    fontSize: "13px",
                    height: "26px",
                  }}
                />
              </Box>
              <IconButton
                size="small"
                onClick={() => setShowBalance(!showBalance)}
                sx={{
                  backgroundColor: "var(--bs-primary3)",
                  color: "var(--bs-primary2)",
                  width: 32,
                  height: 32,
                  "&:hover": {
                    backgroundColor: "var(--bs-primary3)",
                    opacity: 0.8,
                  },
                }}
              >
                {showBalance ? (
                  <VisibilityOffIcon sx={{ fontSize: 18 }} />
                ) : (
                  <VisibilityIcon sx={{ fontSize: 18 }} />
                )}
              </IconButton>
            </Stack>
            <Button
              variant="contained"
              fullWidth
              onClick={() => setShowModal(true)}
              sx={{
                mt: 2,
                background: "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                color: "white",
                textTransform: "none",
                fontWeight: 600,
                fontSize: "14px",
                borderRadius: "10px",
                py: 1,
                boxShadow: "0 4px 12px rgba(102, 126, 234, 0.3)",
                "&:hover": {
                  background:
                    "linear-gradient(135deg, #764ba2 0%, #667eea 100%)",
                  boxShadow: "0 6px 16px rgba(102, 126, 234, 0.4)",
                },
              }}
            >
              View Profile
            </Button>
          </CardContent>
        </Card>

        {/* HELP CENTER SECTION */}
        <Box
          sx={{
            width: "100%",
            color: "var(--bs-primary2)",
            borderRadius: "20px",
            boxShadow: "0 8px 24px rgba(0,0,0,0.12)",
            p: 2,
            border: "1px solid var(--bs-primary3)",
            backgroundColor: "var(--bs-primary1)",
          }}
        >
          <CardContent sx={{ p: 2, "&:last-child": { pb: 2 } }}>
            <Stack direction="row" spacing={1.5} alignItems="center" mb={1.5}>
              <Box
                sx={{
                  width: 36,
                  height: 36,
                  borderRadius: "10px",
                  background:
                    "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <SupportAgentIcon sx={{ color: "white", fontSize: 20 }} />
              </Box>
              <Typography
                variant="h6"
                sx={{
                  color: "var(--bs-primary2)",
                  fontWeight: 600,
                  fontSize: "15px",
                }}
              >
                Help Center
              </Typography>
            </Stack>

            <Stack spacing={1}>
              <Box
                sx={{
                  display: "flex",
                  alignItems: "center",
                  gap: 1,
                  p: 1,
                  borderRadius: "10px",
                  backgroundColor: "var(--bs-primary3)",
                }}
              >
                <Typography sx={{ fontSize: "16px", flexShrink: 0 }}>
                  
                </Typography>
                <Typography
                  variant="body2"
                  sx={{
                    fontSize: "13px",
                    color: "var(--bs-primary2)",
                    fontWeight: 500,
                  }}
                >
                  +91 1242262222
                </Typography>
              </Box>

              <Box
                sx={{
                  display: "flex",
                  alignItems: "flex-start",
                  gap: 1,
                  p: 1,
                  borderRadius: "10px",
                  backgroundColor: "var(--bs-primary3)",
                }}
              >
                <Typography sx={{ fontSize: "16px", flexShrink: 0, mt: 0.2 }}>
                  
                </Typography>
                <Typography
                  variant="body2"
                  sx={{
                    fontSize: "13px",
                    color: "var(--bs-primary2)",
                    fontWeight: 500,
                    wordBreak: "break-word",
                    lineHeight: 1.4,
                  }}
                >
                  support@bankmudraa.com
                </Typography>
              </Box>
            </Stack>

            <Button
              variant="outlined"
              fullWidth
              onClick={() =>
                (window.location.href =
                  "mailto:support@bankmudraa.com?subject=Support Request&body=Hello BankMudraa Support,")
              }
              sx={{
                mt: 1.5,
                color: "var(--bs-primary2)",
                borderColor: "var(--bs-primary3)",
                textTransform: "none",
                fontWeight: 600,
                fontSize: "13px",
                borderRadius: "10px",
                py: 0.8,
                "&:hover": {
                  borderColor: "var(--bs-primary2)",
                  backgroundColor: "transparent",
                },
              }}
            >
              Contact Us
            </Button>
          </CardContent>
        </Box>

        {/* LATEST TRANSACTIONS SECTION */}
        <Box
          sx={{
            width: "100%",
            background: "var(--bs-primary1)",
            borderRadius: "20px",
            boxShadow: "0 8px 24px rgba(0,0,0,0.12)",
            border: "1px solid var(--bs-primary3)",
            // maxHeight: "350px",
            display: "flex",
            flexDirection: "column",
            maxHeight: "350px",
            // p:1,
          }}
        >
          <CardContent
            sx={{
              p: 2,
              "&:last-child": { pb: 2 },
              flex: 1,
              display: "flex",
              flexDirection: "column",
              overflow: "hidden",
            }}
          >
            <Stack direction="row" spacing={1} alignItems="center" mb={1}>
              <Box
                sx={{
                  width: 36,
                  height: 36,
                  borderRadius: "10px",
                  background:
                    "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                  display: "flex",
                  alignItems: "center",
                  justifyContent: "center",
                }}
              >
                <ReceiptLongIcon sx={{ color: "white", fontSize: 20 }} />
              </Box>
              <Typography
                variant="h6"
                sx={{
                  color: "var(--bs-primary2)",
                  fontWeight: 600,
                  fontSize: "15px",
                }}
              >
                Recent Transactions
              </Typography>
            </Stack>

            <Box
              sx={{
                flex: 1,
                overflowY: "scroll",
                overflowX: "hidden",
                mb: 1.5,
                pr: 0.5,
                "&::-webkit-scrollbar": {
                  width: "6px",
                },
                "&::-webkit-scrollbar-track": {
                  background: "rgba(102, 126, 234, 0.1)",
                  borderRadius: "10px",
                },
                "&::-webkit-scrollbar-thumb": {
                  background:
                    "linear-gradient(135deg, #667eea 0%, #764ba2 100%)",
                  borderRadius: "10px",
                  "&:hover": {
                    background:
                      "linear-gradient(135deg, #764ba2 0%, #667eea 100%)",
                  },
                },
              }}
            >
              <Box
                sx={{
                  minWidth: "max-content",
                  whiteSpace: "nowrap",
                }}
              >
                <Transactions data={data} />
              </Box>
            </Box>

            <Button
              variant="text"
              fullWidth
              onClick={() => navigate("/wallet-report")}
              sx={{
                textTransform: "none",
                fontSize: "13px",
                fontWeight: 600,
                color: "var(--bs-primary2)",
                flexShrink: 0,
                "&:hover": {
                  backgroundColor: "transparent",
                  textDecoration: "underline",
                },
              }}
            >
              View All Transactions 
            </Button>
          </CardContent>
        </Box>
      </Box>

      {/* BOOTSTRAP PROFILE MODAL */}
      <ProfileModal
        show={showModal}
        handleClose={() => setShowModal(false)}
        profiledata={profiledata}
      />
    </Grid>
  );
}

export default DaseboardCheckPage;
