const filterApiUser = (content) => {
    let user = localStorage.getItem('userType');
    
    if (user === 'Api User') {
        return null; 
    }

    return content;
};

export default filterApiUser


export const  addAPIUserContent = (content)=>{
    let user = localStorage.getItem('userType');
    if (user === 'Api User') {
        return content; 
    }

    return null;
}