import React from "react";
import { Modal, Button } from "react-bootstrap";
import {
  Box,
  Typography,
  Stack,
  Avatar,
  Divider,
  Grid,
  Paper,
} from "@mui/material";
import { genrateUrl } from "../../../utils/genrateUrl";

const ProfileModal = ({ show, handleClose, profiledata }) => {
  if (!profiledata) return null;

  const business = profiledata?.businessInfo || {};
  const bank = profiledata?.bankDetails || {};
  const userType = profiledata?.user_type_id?.user_type || "-";

  return (
    <Modal
      show={show}
      onHide={handleClose}
      centered
      size="md"
      className="custom-modal"
      backdrop="static"
    >
      <Modal.Header
        closeButton
        style={{ backgroundColor: "var(--bs-primary1)" }}
      >
        <Modal.Title style={{ color: "var(--bs-primary2)" }}>
          Profile Details
        </Modal.Title>
      </Modal.Header>

      <Modal.Body
        style={{
          backgroundColor: "var(--bs-primary1)",
          maxHeight: "70vh",
          overflowY: "auto",
        }}
      >
        {/* Profile Header */}
        <Stack alignItems="center" spacing={1.5} mb={2}>
          <Avatar
            src={profiledata?.profile ? profiledata?.profile
              : `https://ui-avatars.com/api/?name=${profiledata?.pan_name || profiledata?.name
              }&background=random&color=fff&w=50`}
            sx={{
              width: 90,
              height: 90,
              border: "2px solid var(--bs-primary2)",
              boxShadow: "0 0 10px rgba(0,0,0,0.2)",
            }}
          />
          <Typography
            variant="h6"
            sx={{
              fontWeight: 600,
              textAlign: "center",
              color: "var(--bs-primary2)",
            }}
          >
            {profiledata?.name || ""}
          </Typography>
          <Typography
            variant="body2"
            sx={{ color: "var(--bs-primary2)", textAlign: "center" }}
          >
            Code: {profiledata?.refer_id || "N/A"}
          </Typography>
        </Stack>

        {/* Wallet Summary */}
        <Stack
          direction="row"
          spacing={1.5}
          rowGap={2}
          justifyContent="center"
          mb={2}
          flexWrap="wrap"
        >
          {[
            { label: "Main", value: profiledata?.main_wallet },
            { label: "AEPS", value: profiledata?.aeps_wallet },
            { label: "Cashback", value: profiledata?.cashback_wallet },
            { label: "Locking", value: profiledata?.locking_amt },
          ].map((wallet) => (
            <Paper
              key={wallet.label}
              sx={{
                p: 1.5,
                minWidth: 80,
                textAlign: "center",
                backgroundColor: "var(--bs-primary1)",
                border: "1px solid var(--bs-primary2)",
                mb: 1,
              }}
            >
              <Typography
                variant="body2"
                sx={{ color: "var(--bs-primary2)", fontWeight: 600 }}
              >
                {wallet.label}
              </Typography>
              <Typography variant="body2" sx={{ color: "var(--bs-primary2)" }}>
                {wallet.value || 0}
              </Typography>
            </Paper>
          ))}
        </Stack>

        <Divider sx={{ mb: 2, borderColor: "var(--bs-primary2)" }} />

        {/* Personal Details */}
        <Paper
          elevation={0}
          sx={{
            p: 2,
            mb: 2,
            borderRadius: 2,
            backgroundColor: "var(--bs-primary1)",
            border: "1px solid var(--bs-primary2)",
          }}
        >
          <Typography
            variant="subtitle1"
            fontSize="14px"
            fontWeight={700}
            color="var(--bs-primary2)"
            mb={1.5}
            sx={{
              borderLeft: "4px solid var(--bs-primary2)",
              pl: 1,
            }}
          >
            Personal Information
          </Typography>
          <Grid container spacing={1.5}>
            {[
              ["Email", profiledata?.email],
              ["Mobile", profiledata?.mobile],
              ["State", profiledata?.state],
              ["PIN", profiledata?.pinCode],
              ["User Type", userType],
              ["Education", profiledata?.educationQualification],
            ].map(([label, value]) => (
              <Grid item xs={12} sm={6} key={label}>
                <Paper
                  elevation={0}
                  sx={{
                    p: 1.2,
                    borderRadius: 1.5,
                    background: "var(--bs-primary1)",
                    border: "1px dashed var(--bs-primary3)",
                  }}
                >
                  <Typography fontSize="11px" color="var(--bs-primary2)">
                    {label}
                  </Typography>
                  <Typography fontSize="13px" fontWeight={600}>
                    {value || ""}
                  </Typography>
                </Paper>
              </Grid>
            ))}
            <Grid item xs={12}>
              <Paper
                elevation={0}
                sx={{
                  p: 1.2,
                  borderRadius: 1.5,
                  background: "var(--bs-primary1)",
                  border: "1px dashed var(--bs-primary3)",
                }}
              >
                <Typography fontSize="11px" color="var(--bs-primary2)">
                  Address
                </Typography>
                <Typography fontSize="13px" fontWeight={600}>
                  {profiledata?.presentAddr || ""}
                </Typography>
              </Paper>
            </Grid>
          </Grid>
        </Paper>

        {/* Business Details */}
        <Paper
          elevation={0}
          sx={{
            p: 2,
            mb: 2,
            borderRadius: 2,
            backgroundColor: "var(--bs-primary1)",
            border: "1px solid var(--bs-primary2)",
          }}
        >
          <Typography
            variant="subtitle1"
            fontSize="14px"
            fontWeight={700}
            color="var(--bs-primary2)"
            mb={1.5}
            sx={{
              borderLeft: "4px solid var(--bs-primary2)",
              pl: 1,
            }}
          >
            Business Information
          </Typography>
          <Grid container spacing={1.5}>
            {profiledata?.isGstAvailable ? (
              <>
                {[
                  ["GST Number", business?.gst_NUmber],
                  ["Legal Name", business?.legal_name_of_business],
                  ["Business Type", business?.constitution_of_business],
                ].map(([label, value]) => (
                  <Grid item xs={12} sm={6} key={label}>
                    <Paper
                      elevation={0}
                      sx={{
                        p: 1.2,
                        borderRadius: 1.5,
                        background: "var(--bs-primary1)",
                        border: "1px dashed var(--bs-primary3)",
                      }}
                    >
                      <Typography fontSize="11px" color="var(--bs-primary2)">
                        {label}
                      </Typography>
                      <Typography fontSize="13px" fontWeight={600}>
                        {value || ""}
                      </Typography>
                    </Paper>
                  </Grid>
                ))}
              </>
            ) : (
              <Grid item xs={12}>
                <Paper
                  elevation={0}
                  sx={{
                    p: 1.2,
                    borderRadius: 1.5,
                    background: "var(--bs-primary1)",
                    border: "1px dashed var(--bs-primary3)",
                  }}
                >
                  <Typography fontSize="11px" color="var(--bs-primary2)">
                    Shop Name
                  </Typography>
                  <Typography fontSize="13px" fontWeight={600}>
                    {profiledata?.shop_name || ""}
                  </Typography>
                </Paper>
              </Grid>
            )}
            <Grid item xs={12}>
              <Paper
                elevation={0}
                sx={{
                  p: 1.2,
                  borderRadius: 1.5,
                  background: "var(--bs-primary1)",
                  border: "1px dashed var(--bs-primary3)",
                }}
              >
                <Typography fontSize="11px" color="var(--bs-primary2)">
                  Business Address
                </Typography>
                <Typography fontSize="13px" fontWeight={600}>
                  {business?.business_Address ||
                    profiledata?.business_Address ||
                    ""}
                </Typography>
              </Paper>
            </Grid>
            {[
              ["Area", business?.business_Area || profiledata?.business_Area],
              ["City", business?.business_city || profiledata?.business_city],
            ].map(([label, value]) => (
              <Grid item xs={12} sm={6} key={label}>
                <Paper
                  elevation={0}
                  sx={{
                    p: 1.2,
                    borderRadius: 1.5,
                    background: "var(--bs-primary1)",
                    border: "1px dashed var(--bs-primary3)",
                  }}
                >
                  <Typography fontSize="11px" color="var(--bs-primary2)">
                    {label}
                  </Typography>
                  <Typography fontSize="13px" fontWeight={600}>
                    {value || ""}
                  </Typography>
                </Paper>
              </Grid>
            ))}
          </Grid>
        </Paper>

        {/* Bank Details */}
        {bank && (
          <Paper
            elevation={0}
            sx={{
              p: 2,
              mb: 2,
              borderRadius: 2,
              backgroundColor: "var(--bs-primary1)",
              border: "1px solid var(--bs-primary2)",
            }}
          >
            <Typography
              fontSize="14px"
              fontWeight={700}
              color="var(--bs-primary2)"
              mb={1.5}
              sx={{
                borderLeft: "4px solid var(--bs-primary2)",
                pl: 1,
              }}
            >
              Bank Details
            </Typography>

            <Grid container spacing={1.5}>
              {[
                ["Bank Name", bank?.bank_name],
                ["Account Number", bank?.bank_account_number],
                ["IFSC Code", bank?.bank_ifsc],
                ["Account Holder", bank?.bank_account_name],
              ].map(([label, value]) => (
                <Grid item xs={12} sm={6} key={label}>
                  <Paper
                    elevation={0}
                    sx={{
                      p: 1.2,
                      borderRadius: 1.5,
                      background: "var(--bs-primary1)",
                      border: "1px dashed var(--bs-primary3)",
                    }}
                  >
                    <Typography fontSize="11px" color="var(--bs-primary2)">
                      {label}
                    </Typography>
                    <Typography fontSize="13px" fontWeight={600}>
                      {value || ""}
                    </Typography>
                  </Paper>
                </Grid>
              ))}
            </Grid>
          </Paper>
        )}
      </Modal.Body>

      <Modal.Footer style={{ backgroundColor: "var(--bs-primary1)" }}>
        <Button variant="secondary" onClick={handleClose}>
          Close
        </Button>
      </Modal.Footer>
    </Modal>
  );
};

export default ProfileModal;
