import React from "react";
import { ArrowDownward, ArrowUpward, Warning } from "@mui/icons-material";
import { Box, Grid, Card, CardContent, Typography } from "@mui/material";

const formatAmount = (value) => {
  if (!value && value !== 0) return " 0";

  const num = Number(value);

  if (num < 1e5) {
    return ` ${num.toLocaleString('en-IN')}`;
  }

  if (num >= 1e5 && num < 1e7) {
    return ` ${(num / 1e5).toFixed(2)} L`;
  }

  if (num >= 1e7) {
    return ` ${(num / 1e7).toFixed(2)} Cr`;
  }
};


/* Reusable Card */
const StatCard = ({ icon, label, value, gradient }) => (
  <Card
    sx={{
      height: "100%",
      borderRadius: 3,
      position: "relative",
      overflow: "hidden",
      backdropFilter: "blur(10px)",
      background:
        "linear-gradient(180deg, rgba(255,255,255,0.05), rgba(255,255,255,0))",
      border: "1px solid rgba(255,255,255,0.12)",
      transition: "0.35s ease",
      "&:hover": {
        transform: "translateY(-6px)",
        boxShadow: "0 12px 32px rgba(0,0,0,0.18)",
      },
      "&::before": {
        content: '""',
        position: "absolute",
        top: 0,
        left: 0,
        height: "4px",
        width: "100%",
        background: gradient,
      },
    }}
  >
    <CardContent sx={{ p: 3 }}>
      <Box sx={{ display: "flex", alignItems: "center", gap: 2 }}>
        <Box
          sx={{
            width: 52,
            height: 52,
            borderRadius: 2,
            display: "flex",
            alignItems: "center",
            justifyContent: "center",
            background: gradient,
            color: "#fff",
            boxShadow: "0 6px 18px rgba(0,0,0,0.25)",
            flexShrink: 0,
          }}
        >
          {icon}
        </Box>

        <Box sx={{ minWidth: 0 }}>
          <Typography
            sx={{
              fontWeight: 700,
              fontSize: { xs: "17px", sm: "19px", md: "21px" },
              color: "var(--bs-primary2)",
              whiteSpace: "nowrap",
              overflow: "hidden",
              textOverflow: "ellipsis",
            }}
            title={value}
          >
            {value}
          </Typography>

          <Typography
            variant="caption"
            sx={{
              color: "var(--bs-primary2)",
              opacity: 0.75,
              letterSpacing: "0.5px",
              textTransform: "uppercase",
              fontWeight: 600,
            }}
          >
            {label}
          </Typography>
        </Box>
      </Box>
    </CardContent>
  </Card>
);

const DashboardCard = ({ data }) => {
  return (
    <Box sx={{ p: 2, background: "var(--bs-primary3)" }}>
      <Grid container spacing={3}>

        <Grid item xs={12} sm={6} md={4}>
          <StatCard
            icon={<ArrowDownward />}
            label="Credit"
            value={formatAmount(data?.totalCredit)}
            gradient="linear-gradient(135deg, #4CAF50, #81C784)"
          />
        </Grid>

        <Grid item xs={12} sm={6} md={4}>
          <StatCard
            icon={<ArrowUpward />}
            label="Debit"
            value={formatAmount(data?.totalDebit)}
            gradient="linear-gradient(135deg, #F44336, #E57373)"
          />
        </Grid>

        <Grid item xs={12} sm={6} md={4}>
          <StatCard
            icon={<Warning />}
            label="Dispute Requests"
            value={data?.totalDisputeCount ?? 0}
            gradient="linear-gradient(135deg, #FF9800, #FFB74D)"
          />
        </Grid>

      </Grid>
    </Box>
  );
};

export default DashboardCard;



